import _get from 'lodash.get';
import { TableRow, TableCell } from '@material-ui/core';

type TextRowProps<T> = {
	data: T;
	source?: string;
	label: string;
	getValue?: (d: T) => string;
	defaultValue?: string;
};

function TextRow<T>({
	data,
	source = '',
	label,
	getValue,
	defaultValue,
}: TextRowProps<T>) {
	return (
		<TableRow>
			<TableCell variant='head'>{label}</TableCell>
			<TableCell>
				{getValue ? getValue(data) : _get(data, source) || defaultValue || ''}
			</TableCell>
		</TableRow>
	);
}

export default TextRow;
