import { Grid, Link } from '@material-ui/core';
import MuiTypography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import _isEmpty from 'lodash.isempty';
import {
	Close as CloseIcon,
	InsertDriveFile as InsertDriveFileIcon,
} from '@material-ui/icons';
import { TFile } from '../../../../types';

const useStyles = makeStyles(theme => ({
	uploadFile: {
		position: 'relative',
		overflow: 'hidden',
		display: 'flex',
		alignItems: 'center',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
		'&:hover $imageHover': {
			opacity: 1,
			zIndex: 1,
			cursor: 'pointer',
		},
	},
	imageHover: {
		opacity: 0,
	},
	removeButton: {
		margin: 'auto',
	},
	removeIcon: {
		color: theme.palette.primary.main,
	},
	name: {
		padding: '12px 0',
		marginBottom: '12px',
		position: 'relative',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
		'&:hover $imageHover': {
			opacity: 1,
			zIndex: 1,
		},
	},
}));

type Props = {
	file: TFile;
	onRemove: Function;
};

const renderMedia = (file: TFile) => {
	const { preview, name } = file;

	const props = _isEmpty(preview)
		? ''
		: {
				component: Link,
				href: preview,
				target: '_blank',
		  };

	return (
		<MuiTypography variant='body2' color='primary' display='block' {...props}>
			{name}
		</MuiTypography>
	);
};

const PreviewFile = (props: Props) => {
	const { file, onRemove } = props;
	const classes = useStyles();
	return (
		<Grid item xs={12} md={6} lg={4} alignContent='center' alignItems='center'>
			<div className={classes.uploadFile}>
				<InsertDriveFileIcon />
				{renderMedia(file)}
				<div className={classes.imageHover}>
					<CloseIcon onClick={() => onRemove()} className={classes.removeIcon} />
				</div>
			</div>
		</Grid>
	);
};

export default PreviewFile;
