import {
	Grid,
	Table,
	TableBody,
	TableContainer,
	Alert,
	useTheme,
	Button,
	Stack,
	Divider,
	CardContent,
	CardActions,
	CircularProgress,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { useQuery, gql } from '@apollo/client';
import { useEffect } from 'react';
import MainCard from '../../../ui-component/cards/MainCard';
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import SubCard from '../../../ui-component/cards/SubCard';
import { gridSpacing } from '../../../store/constant';
import { TOffer, TUploadFile } from '../../../types';
import { getOffer, getOfferDocPutUrls } from '../../../graphql/queries';
import Loader from '../../../ui-component/Loader';
import { getOfferPrice } from '../../../helpers/offer';
import TextRow from '../components/TextRow';
import UploadFile from '../components/UploadFile';
import OfferActions from './OfferActions';
import { FILE_CONFIG } from './config';
import { useUploadFiles } from '../../../hooks';
import config from '../../../config';
import { convertToFile } from '../../../helpers/file';

type Props = {
	id: string;
};

const DOCUMENT_TYPE = {
	PA: 'SignedPurchaseAgreement',
	SCAR: 'SCAR',
};

const OfferEdit = ({ id }: Props) => {
	const { loading, error, data, refetch } = useQuery(gql(getOffer), {
		variables: { OfferId: id },
	});

	const generateOptions = (documentType = DOCUMENT_TYPE.SCAR) => ({
		name: 'getOfferDocPutUrls',
		query: getOfferDocPutUrls,
		generateVariables: (files: TUploadFile[]) => ({
			Documents: files,
			OfferId: id,
			DocumentType: documentType,
		}),
		refetch,
	});

	const {
		files: PAFiles,
		onFilesChange: onPAChange,
		uploadFiles: uploadPA,
		isUploadLoading: isPAUploading,
	} = useUploadFiles(generateOptions(DOCUMENT_TYPE.PA));

	const {
		files: SCARFiles,
		onFilesChange: onSCARChange,
		uploadFiles: uploadSCAR,
		isUploadLoading: isSCARUploading,
	} = useUploadFiles(generateOptions());

	const offer = _get(data, 'getOffer', {});
	const currentStepId = +_get(offer, 'StepId', 0);
	const offerNo = _get(offer, 'OfferNo', '');
	const theme = useTheme();

	useEffect(() => {
		const offer = _get(data, 'getOffer', {});
		const docPA = _get(offer, 'SignedPurchaseAgreement') || [];
		const docSCAR = _get(offer, 'SCAR') || [];

		if (!_isEmpty(docPA)) onPAChange(docPA.map(convertToFile));
		if (!_isEmpty(docSCAR)) onSCARChange(docSCAR.map(convertToFile));
	}, [data]);

	if (loading) return <Loader />;

	if (error && _isEmpty(offer)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Offer not found
			</Alert>
		);
	}

	const onUpdate = () => {
		uploadPA();
		uploadSCAR();
	};

	const isShowPA = currentStepId === config.offerSteps.paBeingCreated;
	const isShowSCAR = currentStepId === config.offerSteps.reviewScarRequest;
	const isUploading = isPAUploading || isSCARUploading;

	return (
		<MainCard
			title={offerNo}
			content={false}
			secondary={<OfferActions offer={offer} />}
		>
			<CardContent>
				<Grid container spacing={gridSpacing}>
					<Grid item lg={6} xs={12}>
						<SubCard title='Offer Information'>
							<TableContainer>
								<Table
									sx={{
										'& td': {
											borderBottom: 'none',
										},
									}}
									size='small'
								>
									<TableBody>
										<TextRow<TOffer> data={offer} source='OfferNo' label='Offer No' />
										<TextRow<TOffer>
											data={offer}
											source='PropertyData.FullAddress'
											label='Property Address'
										/>
										<TextRow<TOffer>
											data={offer}
											source='PropertyData.OwnerNames'
											label='Sellers'
										/>
										<TextRow<TOffer>
											data={offer}
											getValue={getOfferPrice}
											label='Offer Price'
										/>
										<TextRow<TOffer>
											data={offer}
											source='Funding.PurchaseType'
											label='Funding'
										/>
										<TextRow<TOffer>
											data={offer}
											source='Funding.LoanType'
											label='Loan Type'
										/>
										<TextRow<TOffer>
											data={offer}
											source='Funding.DownPayment'
											label='Down Payment'
										/>
										<TextRow<TOffer> data={offer} source='Step.name' label='Status' />
									</TableBody>
								</Table>
							</TableContainer>
						</SubCard>
					</Grid>
					<Grid item lg={6} xs={12}>
						<Grid container direction='column' spacing={gridSpacing}>
							<Grid item xs={12}>
								<SubCard title='Signed Purchase Agreement'>
									<UploadFile
										name='PA'
										files={PAFiles}
										onDrop={onPAChange}
										accept={FILE_CONFIG.type}
										disabled={!isShowPA}
									/>
								</SubCard>
							</Grid>
							<Grid item xs={12}>
								<SubCard title='SCAR'>
									<UploadFile
										name='SCAR'
										files={SCARFiles}
										onDrop={onSCARChange}
										accept={FILE_CONFIG.type}
										disabled={!isShowSCAR}
									/>
								</SubCard>
							</Grid>
						</Grid>
					</Grid>
				</Grid>
			</CardContent>
			<Divider />
			<CardActions>
				<Grid item xs={12}>
					<Stack direction='row' justifyContent='flex-end'>
						<AnimateButton>
							<Button
								color='primary'
								variant='contained'
								type='submit'
								disabled={(!isShowPA && !isShowSCAR) || isUploading}
								onClick={onUpdate}
							>
								{isUploading ? (
									<CircularProgress size='1.5rem' color='secondary' />
								) : (
									'Update'
								)}
							</Button>
						</AnimateButton>
					</Stack>
				</Grid>
			</CardActions>
		</MainCard>
	);
};

export default OfferEdit;
