/* eslint-disable camelcase */
import { Grid, Button } from '@material-ui/core';

const buttonStylesByStep: { [stepId: number]: TStyle } = {
	150: {
		variant: 'outlined',
		color: 'secondary',
	},
	200: {
		variant: 'contained',
		color: 'primary',
	},
	500: {
		variant: 'contained',
		color: 'primary',
	},
	800: {
		variant: 'contained',
		color: 'primary',
	},
	910: {
		variant: 'text',
		color: 'error',
	},
};

type TStyle = {
	variant: string;
	color: string;
};

export type TAction = {
	action: string;
	form_action: boolean | null;
	next_step_id: number;
};

type Props = {
	action: TAction;
	onClick: () => void;
	disabled?: boolean;
};

export const ActionButton = ({ action, onClick, disabled }: Props) => {
	const { action: content, next_step_id: nextStepId } = action;
	const buttonStyle = buttonStylesByStep[nextStepId] as any;
	return (
		<Grid item>
			<Button type='button' onClick={onClick} disabled={disabled} {...buttonStyle}>
				{content}
			</Button>
		</Grid>
	);
};
