import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { TOffer } from '../types';
import config from '../config';
import { formatCurrency } from './currency';

const { offerSteps } = config;

export const getOfferPrice = (offer: TOffer): string => {
	let price;

	if (+_get(offer, ['Step', 'id']) === offerSteps.counteredBySeller)
		price = _get(offer, ['CounterOffer', 'SellerCounter', 'OfferPrice']);
	else price = _get(offer, ['CounterOffer', 'BuyerCounter', 'OfferPrice']);

	return price ? formatCurrency(price) : '';
};

export const isOfferActionStep = (offer: TOffer, expectationStep: number) =>
	_get(offer, 'step.id') === expectationStep;

export const isDocumentExits = (offer: TOffer, type: string): boolean =>
	!_isEmpty(_get(offer, type));
