import { Grid, InputLabel, Button } from '@material-ui/core';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import { makeStyles } from '@material-ui/styles';
import clsx from 'clsx';
import { useDropzone } from 'react-dropzone';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { useEffect } from 'react';
import Preview from './Preview';
import PreviewFile from './PreviewFile';
import { useSnackBar } from '../../../../hooks';
import { TFile } from '../../../../types';
import media from './MediaType';

const useStyles = makeStyles(() => ({
	fileInput: {
		background: '#fcfcfe',
		width: '100%',
		marginBottom: '12px',
		borderWidth: 2,
		borderRadius: 2,
		borderColor: '#eee',
		borderStyle: 'dashed',
	},
	activeStyle: {
		borderColor: '#2196f3',
	},
	fileContainer: {
		padding: '30px 0',
		textAlign: 'center',
		borderRadius: '4px',
		cursor: 'pointer',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
	},
	uploadImage: {
		position: 'relative',
		overflow: 'hidden',
		borderRadius: '4px',
		cursor: 'pointer',
		width: '100%',
		minHeight: '100px',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
		'&:hover $imageHover': {
			opacity: 1,
			zIndex: 1,
		},
	},
	imageHover: {
		position: 'absolute',
		opacity: 0,
		zIndex: -1,
		left: '0',
		top: '0',
		background: 'rgba(255, 255, 255, .8)',
		width: '100%',
		height: '100%',
		display: 'inline-flex',
		transition: 'all 0.3s ease',
	},
	removeButton: {
		margin: 'auto',
	},
}));

type Props = {
	files: TFile[];
	accept?: string;
	maxFiles?: number;
	disabled?: boolean;
	name?: string;
	onDrop: (files: TFile[]) => void;
};

const UploadFile = (props: Props) => {
	const {
		files,
		accept = 'image/*',
		maxFiles = 10,
		onDrop,
		disabled,
		name,
	} = props;
	const classes = useStyles();
	const { getRootProps, getInputProps, isDragActive, fileRejections } =
		useDropzone({
			onDrop,
			accept,
			maxFiles,
		});
	const showSnackBar = useSnackBar();

	useEffect(() => {
		if (_isEmpty(fileRejections)) return;
		const message = _get(fileRejections, '0.errors.0.message', '');
		showSnackBar({
			message,
			variant: 'alert',
			alertSeverity: 'error',
		});
	}, [fileRejections]);

	const removeAt = (index: number) => () => {
		const newFiles = [...files];
		newFiles.splice(index, 1);
		onDrop(newFiles);
	};

	return (
		<>
			<Button
				{...getRootProps({
					className: clsx(classes.fileInput, {
						[classes.activeStyle]: isDragActive,
					}),
				})}
				disabled={disabled}
			>
				<InputLabel className={classes.fileContainer}>
					<CloudUploadIcon />
					Drop file here to upload
				</InputLabel>
			</Button>
			<input {...getInputProps()} name={name} />
			<Grid container spacing={1}>
				{files.map((file, index) => {
					const { type } = file;
					const PreviewComponent = type === media.pdf ? PreviewFile : Preview;
					return (
						<PreviewComponent
							file={file}
							key={file.name}
							onRemove={removeAt(index)}
						/>
					);
				})}
			</Grid>
		</>
	);
};

export default UploadFile;
