import { Grid, CardMedia, Fab, Typography } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/styles';
import { TFile } from '../../../../types';

const useStyles = makeStyles(() => ({
	uploadImage: {
		position: 'relative',
		overflow: 'hidden',
		borderRadius: '4px',
		cursor: 'pointer',
		width: '100%',
		minHeight: '100px',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
		'&:hover $imageHover': {
			opacity: 1,
			zIndex: 1,
		},
	},
	imageHover: {
		position: 'absolute',
		opacity: 0,
		zIndex: -1,
		left: '0',
		top: '0',
		background: 'rgba(255, 255, 255, .8)',
		width: '100%',
		height: '100%',
		display: 'inline-flex',
		transition: 'all 0.3s ease',
	},
	removeButton: {
		margin: 'auto',
	},
	name: {
		padding: '12px 0',
		marginBottom: '12px',
		position: 'relative',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
		'&:hover $imageHover': {
			opacity: 1,
			zIndex: 1,
		},
	},
}));

type Props = {
	file: TFile;
	onRemove: Function;
};

const Preview = (props: Props) => {
	const { file, onRemove } = props;
	const classes = useStyles();

	return file.preview ? (
		<Grid item xs={12} md={6} lg={4} alignContent='center' alignItems='center'>
			<div className={classes.uploadImage}>
				<CardMedia component='img' src={file.preview} title='file' />
				<div className={classes.imageHover}>
					<Fab
						color='secondary'
						size='small'
						className={classes.removeButton}
						onClick={() => onRemove()}
					>
						<CloseIcon />
					</Fab>
				</div>
			</div>
		</Grid>
	) : (
		<Grid item xs={12} alignContent='center' alignItems='center'>
			<div className={classes.name}>
				<Typography>{file.name}</Typography>
				<div className={classes.imageHover}>
					<Fab
						color='secondary'
						size='small'
						className={classes.removeButton}
						onClick={() => onRemove()}
					>
						<CloseIcon />
					</Fab>
				</div>
			</div>
		</Grid>
	);
};

export default Preview;
